import { Metadata } from 'next';
import { notFound } from 'next/navigation';
import { CONTENT } from '../../../data/content';
import { getAllPosts, getPostBySlug } from '../../../lib/posts';
import BlogPost from './BlogPost';

type PageProps = {
  params: Promise<{
    slug: string;
  }>;
};

export async function generateMetadata({ params }: PageProps): Promise<Metadata> {
  // slug ÊòØÊñáÁ´†ÂêçÁß∞
  const { slug } = await params;
  const post = getPostBySlug(slug);

  if (!post) {
    return {
      title: 'Blog Post Not Found'
    };
  }

  return {
    title: `${post.title} | Heiheihoho's Portfolio`,
    description: post.excerpt,
    openGraph: {
      title: post.title,
      description: post.excerpt,
      url: `https://neo-brutalism.example.com/blog/${slug}`,
      type: 'article'
    },
    twitter: {
      card: 'summary_large_image',
      title: post.title,
      description: post.excerpt
    }
  };
}

export default async function BlogPostPage({ params }: PageProps) {
  const { slug } = await params;

  const postsEn = getAllPosts('en');
  const postsZh = getAllPosts('zh');
  const postEn = postsEn.find((post) => post.slug === slug) ?? null;
  const postZh = postsZh.find((post) => post.slug === slug) ?? null;
  const fallbackEn = CONTENT.en.blog.find((post) => post.slug === slug) ?? null;
  const fallbackZh = CONTENT.zh.blog.find((post) => post.slug === slug) ?? null;

  if (!postEn && !postZh && !fallbackEn && !fallbackZh) {
    notFound();
  }

  return <BlogPost posts={{ en: postEn ?? fallbackEn, zh: postZh ?? fallbackZh }} />;
}

export async function generateStaticParams() {
  const slugs = getAllPosts().map(post => post.slug);
  const allSlugs = Array.from(new Set(slugs));

  return allSlugs.map(slug => ({ slug }));
}

